@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#audio{
  position: absolute;
  top: 0;
  z-index: 100;
  background-color: inherit;
}

.App{
  min-height: 170vh;
  overflow-x: hidden;
  background-color: #08AEEA;
  /* background-image: linear-gradient(0deg, #08AEEA 0%, #2AF598 100%); */

}

.head{
  background-image: url(/nguoi-yeu-cu-2/static/media/img-sky.719a4789.jpg);
  background-position: center;
  background-size: cover;
  height: 170vh;
  position: relative;
}

.bird{
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  background-image: url(/nguoi-yeu-cu-2/static/media/giphy-1.4b8072e2.gif);
  width: 100px;
  height: 100px;
  background-position: center;
  background-size: cover;
  z-index: 5;
  -webkit-animation:  bird-animation 25s infinite ease-in;
          animation:  bird-animation 25s infinite ease-in;
}


@-webkit-keyframes bird-animation {
  from{
    right: -300px;
  }
  to{
    right: 130vw;
  }
}


@keyframes bird-animation {
  from{
    right: -300px;
  }
  to{
    right: 130vw;
  }
}

.head::after{
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  width: 0;
  background-image: url(/nguoi-yeu-cu-2/static/media/img-land.096d05e4.png);
  width: 100vw;
  height: 150vh;
  background-position: center;
  background-size: cover;
  z-index: 10;
}

.head::before{
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: linear-gradient(to top, #08AEEA,transparent);
  z-index: 1000;
}

.title{
  display: block;
  padding-top: 24px;
  font-size: 80px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-weight: 800;
  font-family: 'Architects Daughter', cursive;
  color: #E52B50;
  transition: 0.5s ease;
  -webkit-animation: title-anim 3s infinite ease-in-out;
          animation: title-anim 3s infinite ease-in-out;  
  margin: auto;
  cursor: default;
}


.title:hover {
  color:#f08c91;
}

@-webkit-keyframes title-anim {
  from{
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50%{
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100%{
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

@keyframes title-anim {
  from{
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
  50%{
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }
  100%{
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }
}

.get-started{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
  margin-top: 16vh;
}

.get-started h3{
  font-size: 100px;
  word-spacing: 7px;
  letter-spacing: 3px;
  font-family: 'Dancing Script', cursive;
  padding: 12px;
  font-weight: 800;
}

.started-btn{
  width: 100%;
  height:50px;
  border-radius: 100px;
  background-color: rgba(238, 238, 238, 0.37);
  border: none;
  box-shadow: 0 1px 10px 1px black ;
  font-size: 24px;
  font-family: 'Courier New', Courier, monospace;
  cursor: pointer;
  position: relative;
  z-index: 100;
  transition: 0.5s;
}

.started-btn:hover {
  background-color: rgb(240, 89, 89);
  font-weight: 800;
  font-size: 28px;
}

.character{
  position: relative;
  padding-bottom: 50px;
}

.character::after{
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-image: linear-gradient(to top, #fff,transparent);
}

.frames{
  width: 100%;
}

@media (max-width: 750px) {
  .title{
    font-size: 50px;
  }
}

.cake{
  width: 100%;
  height: 600px;
  background-image: url(/nguoi-yeu-cu-2/static/media/cake.41e5f84b.gif);
  background-position: center;
  background-repeat: no-repeat;
  /* background-color: rgba(252, 104, 185, 0.795); */
  background-color: white;
  position: relative;
}

.cake-btn{
  position: absolute;
  bottom: 60px;
  left: 40%;
  width: 20%;
  height: 40px;
  background-color: red;
  border: none;
  color: white;
  cursor: pointer;
  font-size:18px;
  border-radius: 15px;
}
.destination-container{
    overflow: hidden;
}
.destination-hinh-anh img{
    max-width: 600px;
    max-height: 600px;border-radius: 10px;
    transition: 0.2s;
}

.destination-hinh-anh img:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}
.travel-container{
    background: linear-gradient(to bottom,white,rgba(255, 175, 27, 0.596));
}

.travel-place{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin-top: 48px;
}

.travel-place h1{
    font-weight: 800;
    transition: 0.3s;
}

.travel-place h1:hover{
    color: red;
}





*{
    font-family:  'Courier New', Courier, monospace;
    font-weight: 200;
    
}

.profile-container{
   
    display: flex;
    justify-content: space-around;
    width: 90%;
    margin: auto;
    background-color:#fff;
    align-items: center;
    padding: 36px;
    box-shadow: 0 0 15px 0 black; 
    margin-top: 24px;
    border-radius: 36px;
}

.avt{
    width: 30%;
    border-radius: 50%;
    overflow: hidden;
    box-shadow: 0 0 10px 0  black;
    
}

.avt-img{
    width: 100%;
    transition: 0.5s;
}

.avt-img:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.information{
    width: 60%;
    font-size: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    
}

.detail{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    height: 70%;
}

.information strong{
    font-weight: 800;
    color: white;
}

.information .name{
    font-size: 72px;
    padding-bottom: 12px;
    font-weight: 800;
    color: rgb(14, 41, 131);
    transition: 0.5s;
}

.name:hover{
    color: rgba(27, 199, 170, 0.548);
}

.det{

    background-color: #7CB9E8;
    padding: 16px;
    color: white;
    margin: 8px;
    border-radius: 10px;

}


@media (max-width: 1100px) {
    .profile-container{
      width: 96%;
    }
    .detail{
        font-size: 16px;
    }
    .information .name{
        font-size: 48px;
    }
}

@media (max-width: 750px){
    .profile-container{
        flex-direction: column;
    }
    .avt{
        width: 50%;
        margin-bottom: 12px;
    }
    .information {
        width: 90%;
    }

}


.card-container{
    box-shadow: 0 1px 5px 0 black;
    padding: 12px 36px;
    float: left;
    width: 100%;
    height: 100%;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.7s;
}

.card-container:hover{
    background: linear-gradient(to left,red,transparent);
}

